#sliderType .container {
    width: 100%;
}
.slider__inner {
    margin-top: 70px;
}
.slider {
    position: relative;
}
.slider__img {
    background: url(../img/slider01_bg01.jpg) no-repeat center / cover;
}
.slider__img .desc {
    width: 1160px;
    margin: 0 auto;
    padding: 100px 20px;
}
.slider__img .desc span {
    font-size: 16px;
    background-color: #000;
    color: white;
    padding: 1px 14px 0 14px;
    border-radius: 30px;
    text-transform: uppercase;
    margin-bottom: 10px;
    display: inline-block;
}
.slider__img .desc h3 {
    font-size: 110px;
    font-weight: 500;
    line-height: 1;
    color: white;
    text-transform: uppercase;
    margin-bottom: 16px;
    margin-left: -8px;
}
.slider__img .desc p {
    font-size: 20px;
    font-weight: 300;
    color: #fff;
    line-height: 1.35;
    margin-bottom: 130px;
}
.slider__img .desc .btn a {
    font-size: 16px;
    background-color: #fff;
    padding: 11px 50px;
    display: inline-block;
}
.slider__img .desc .btn a.black {
    background: #000;
    color: #fff;
}
.slider__arrow a {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height: 56px;
    background-image: url(../img/slider01_icon.svg);
}
.slider__arrow a.left {
    left: 20px;
}
.slider__arrow a.right {
    right: 20px;
    background-position: -50px 0;
}
.slider__dot {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 25px;
}
.slider__dot a {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url(../img/slider01_icon.svg);
    background-position: -20px -70px;
}
.slider__dot a.active {
    background-position: 0px -70px;
}
.slider__dot a.play {
    background-position: -40px -70px;
}
.slider__dot a.stop {
    background-position: -60px -70px;
}

@media (max-width: 1280px) {
    .slider__img .desc {
        width: 100%;
        box-sizing: border-box;
    }
    .slider__img .desc span {
        margin-left: 50px;
    }
    .slider__img .desc h3 {
        font-size: 80px;
        margin-left: 50px;
    }
    .slider__img .desc p {
        font-size: 20px;
        margin-left: 50px;
    }
    .slider__img .desc .btn {
        display: flex;
        justify-content: center;
    }
    .slider__img .desc .btn a {
        font-size: 16px;
        background-color: #fff;
        padding: 11px 50px;
        display: inline-block;
        margin: 5px;
    }
    .slider__img .desc .btn a.black {
        background: #000;
        color: #fff;
    }
}
@media (max-width: 768px) {
    .slider__img .desc h3 {
        font-size: 50px;
        margin-left: 50px;
    }
    .slider__inner {
        margin-top: 70px;
        margin-bottom: 70px;
    }
    .slider__img .desc p {
        font-size: 12px;
    }
}

/* swiper */
.swiper-slide {
    background: url(../img/slider01_bg01.jpg) no-repeat center / cover;
}
.swiper-slide .desc {
    width: 1160px;
    margin: 0px auto;
    padding: 100px 0px;
}
.swiper-slide .desc span {
    font-size: 16px;
    background-color: #000;
    color: white;
    padding: 1px 14px 0 14px;
    border-radius: 30px;
    text-transform: uppercase;
    margin-bottom: 10px;
    display: inline-block;
}
.swiper-slide .desc h3 {
    font-size: 110px;
    font-weight: 500;
    line-height: 1;
    color: white;
    text-transform: uppercase;
    margin-bottom: 16px;
    margin-left: -8px;
}
.swiper-slide .desc p {
    font-size: 20px;
    font-weight: 300;
    color: #fff;
    line-height: 1.35;
    margin-bottom: 130px;
}
.swiper-slide .desc .btn {
}
.swiper-slide .desc .btn a {
    font-size: 16px;
    background-color: #fff;
    padding: 11px 50px;
    display: inline-block;
}
.swiper-slide .desc .btn a.black {
    background: #000;
    color: #fff;
}

.swiper-button-next,
.swiper-button-prev {
    width: 30px !important;
    height: 56px !important;
    background-image: url(../img/slider01_icon.svg);
}
.swiper-button-next {
    background-position: -50px 0px;
}
.swiper-button-next:after,
.swiper-button-prev:after {
    opacity: 0;
}
.swiper-pagination-bullet {
    width: 16px !important;
    height: 16px !important;
    opacity: 0.5 !important;
}
.swiper-pagination-bullet-active {
    background-position: 0px -70px !important;
    opacity: 1 !important;
    background-color: white !important;
}
.swiper-button {
    position: absolute;
    left: 50%;
    bottom: 24px;
    z-index: 1000;
    margin-left: 40px;
    display: flex;
}
.swiper-button-stop {
    background-image: url(../img/slider01_icon.svg);
    background-position: -60px -70px;
    width: 16px;
    height: 16px;
    cursor: pointer;
    margin-left: 5px;
}
.swiper-button-play {
    background-image: url(../img/slider01_icon.svg);
    background-position: -40px -70px;
    width: 16px;
    height: 16px;
    cursor: pointer;
    margin-left: 5px;
}
/* media */
@media (max-width: 1280px) {
    .swiper-slide .desc h3 {
        font-size: 70px;
    }
    .swiper-slide .desc {
        width: 100%;
        text-align: center;
    }
}
@media (max-width: 600px) {
    .swiper-slide .desc h3 {
        font-size: 42px;
    }
    .swiper-slide .desc {
        margin: 0 auto;
        padding: 100px 20px;
        box-sizing: border-box;
    }
    .swiper-slide .desc p {
        font-size: 16px;
    }
}
