/* textType */
.text__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.text {
    width: 32%;
    margin-bottom: 2%;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 10px;
    transition: background 0.4s;
}
.text:hover {
    background: #f5f5f5;
}
.text:hover .text__title{
    color: #000;
}
.text__title {
    font-size: 24px;
    line-height: 1;
    margin-bottom: 10px;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: relative;
    padding-top: 75px;
}
.text__title::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background: url(../img/text01_icon01.svg);
}
.text.t1 .text__title::before {
    background-position: 0 0;
}
.text.t2 .text__title::before {
    background-position: -60px 0;
}
.text.t3 .text__title::before {
    background-position: -120px 0;
}
.text.t4 .text__title::before {
    background-position: -180px 0;
}
.text.t5 .text__title::before {
    background-position: -240px 0;
}
.text.t6 .text__title::before {
    background-position: -300px 0;
}
.text__desc {
    font-size: 18px;
    font-weight: 300;
    line-height: 1.4;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3; 
    -webkit-box-orient: vertical;
    color: #666;
    margin-bottom: 20px;
}
.text__btn {
    font-size: 16px;
    line-height: 1;
    text-decoration: underline;
    color: #666;
}

@media(max-width: 768px){
    #textType > span {
        display: none;
    }
    .text__desc {
        font-size: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 4; 
        -webkit-box-orient: vertical;
    }
    .text {
        width: 50%;
        margin-bottom: 0px;
        padding: 10px;
    }
}