.banner__inner {
    background-image: url(../img/banner01_bg01.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-align: center;
    padding: 120px 0;
    color: black;
}
.banner__inner .title {
    font-size: 50px;
    line-height: 1;
    font-weight: 500;
    margin-bottom: 40px;
}
.banner__inner .desc {
    font-size: 24px;
    line-height: 1.5;
    font-weight: 300;
    margin-bottom: 70px;
    word-break: keep-all;
}
.banner__inner .desc a {
    color: black;
    display: block;
}
.banner__inner .desc a:hover {
    text-decoration: underline;
}
.banner__inner .small {
    font-size: 16px;
    text-decoration: underline;
}
@media(max-width: 786px) {
    .banner__inner .title {
        font-size: 42px;
    }
    .banner__inner .desc {
        font-size: 18px;
    }
}